.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background-color: #000000;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
}

.loading-text {
  font-family: 'CustomFont', serif;
  font-size: 2rem;
  color: #ff6f21;
}
